.nav-bar {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: white;
}

.nav-tab {
  background-color: inherit;
  border-bottom: 3px solid darkgray;
  padding-bottom: 3px;
  color: black;
  margin: 16px;
  text-align: center;
  text-decoration: none;
  font-size: 24px;
  font-weight: bold;
  cursor: pointer;
}

.actual-body {
  background-color: inherit;
}

.statement {
  font-family: Helvetica, Arial, sans-serif;
  font-size: 72px;
  text-align: center;
  margin: 24px;
}

.tagline {
  font-family: Helvetica, Arial, sans-serif;
  font-size: 36px;
  text-align: center;
}

.page-title {
  font-family: Helvetica, Arial, sans-serif;
  font-size: 62px;
  font-weight: bold;
  text-align: center;
  margin: 24px;
}

.travel-entry {
  background-color: lightblue;
  border-top: 1px solid darkgray;
  border-bottom: 1px solid darkgray;
  display: flex;
  padding: 8px;
}

.video-container {
  flex-grow: 1;
  overflow: hidden;
}